html,
body {
	margin: 0%;
	padding: 0%;
	overflow-x: hidden;
}

.hover-shadow:hover {
	text-shadow: 0px 0px 25px black;
	transition: .3s;
}

.header {
	width: 100vw;
	height: inherit;
	background-color: black;
	position: fixed;
	z-index: 100;
}

.t-center {
	text-align: center;
}

#mobile_device {
	visibility: hidden;
  }

.reset-btn {
	font-family: Poppins;
	border: none;
	background-color: gray;
	color: white;
	cursor: pointer;
	margin-top: 5vh;
}
.info {
    position:absolute;
    font-family: 'Poppins', sans-serif;
    color: whitesmoke;
    margin-top: 65vh;
    margin-left:70vw;
}

.updatedInfoMargins {
	margin-top: 80vh;
    margin-left:90vw;
}

.instructions{
    font-family: 'Poppins', sans-serif;
    position: absolute;
    color: whitesmoke;
    font-size: small;
    margin-top: 2vh;
    margin-left:2vw;
}

.githubLink {
    color: whitesmoke;
}

.navigator-main {
	position: fixed;
	right: 0px;
	margin-top: 50vh;
	transform: translateY(-50%);
	font-family: 'Poppins', sans-serif;
	font-weight: 700;
	width: 10vw;
	text-align: right;
	padding-right: 1vw;
	transition: .5s;
	text-transform: uppercase;
	font-size: 1rem;
}

.whiteText {
	color: whitesmoke;
}

@media only screen and (max-width: 600px) {
	#mobile_device {
		visibility: visible;
	}
	html,body{
		overflow-y: hidden;
	}
  }